import React from 'react'
import { contact, section7Title, social } from '../../profile'

const Contact = () => {
    
    return (
        <div className="parallax">
                <div data-aos="zoom-in-up" data-aos-once="true" className="git-form">
                    <>
                <div className="git-head-div text-center mx-auto">
                        <h1 id="Contact" className="git-head">{section7Title}</h1>
                </div>
                </>
                <div className="container">
            <div className="git-cont row">
                <div className="col-12 col-sm-6 half">
                <form action={contact.contactUrl ? contact.contactUrl : "https://form.jotform.com/211257857518059"} method={contact.contactUrl ? "POST" : "GET"}>
                        <input type="text" id="fname" name="firstname" placeholder="Your name" required></input>
                        <input type="mail" id="mailid" name="Email" placeholder="Email Address" required></input>
                        <input type="text" id="sub" name="Subject" placeholder="Subject" required></input>
                        <textarea id="msg" name="message" placeholder="Message" required></textarea>
                        <button style={{cursor: 'pointer'}} type="submit"><label style={{cursor: 'pointer'}} id="not-dark">Send Message</label></button>
                    </form>
                </div>
                <div className="col-12 col-sm-6 half">
                    <p className="lead">
                        {contact.pitch}        
                    </p>
                <div className="d-flex justify-content-center align-items-center flex-column">
                <div className="inline-block">
                    {social.linkedin && <a title="Visit Linkedin profile" rel="noopener noreferrer" target="_blank"  href={"https://www.linkedin.com/feed/"}><i className="fab fa-linkedin"></i></a>}
                    {social.facebook && <a title="Visit Facebok profile" rel="noopener noreferrer" target="_blank" href={"https://www.facebook.com/nathamiqbal.natha"}><i className="fab fa-facebook"></i></a>}
                    
                    {social.github && <a title="Visit Github profile" rel="noopener noreferrer" target="_blank" href={"https://github.com/nathamiqbal" }><i className="fab fa-github"></i></a>}<br/>
                </div>
                    {social.resume && <a title="Download biodata" href={"https://drive.google.com/file/d/1UGrTe6y5O3e1voOeKk62XihL0qI8wEOq/view?usp=sharing"} download><i className="fas fa-download"></i></a>}
                    
                </div>
                </div>
            </div>
        </div>
     </div> 
     </div>


        
    )
    
}

export default Contact
